
*{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

body{
  padding:60px;
}

input{
  color:green;
  font-size: 1.2rem;
  width: 100%;
}


.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border:1px solid grey;
  border-radius: hidden;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  text-align: center;
  padding: 5px;

}

.card img{
  max-height: 170px;
}